@font-face {
    font-family: "BTTF";
    src: url("fonts/BTTF.ttf");
}

.banner{
    height:100vh;
}

.background{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height:100vh;
    animation: animate 32s ease-in-out infinite;
    background-size: cover;
}


@keyframes animate{
    0%, 100%{
        background-image: url("./images1/bi9.jpg");
        filter: grayscale(100%);
    }
    15%{
        background-image: url("./images1/bi29.jpeg");
    }
    30%{
        background-image: url("./images1/bi21.jpeg");
    }
    45%{
        background-image: url("./images1/bi19.jpg");
    }
    60%{
        background-image: url("./images1/bi10.jpg");
    }
    75%{
        background-image: url("./images1/bi8.jpg");
    }
    90%{
        background-image: url("./images1/bi17.jpg");
    }   
}


.deloreanImg{
    width:100%;
}

.container{
    width:800px;
    margin-right:auto;
    margin-left:auto;
    position:absolute;
    bottom:-120px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    
} 

.spacyBackground:hover{
    background-image: url(https://cdn.mos.cms.futurecdn.net/NUiwBaHbarqcAqqdpPqJu8-320-80.jpg);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
  
.logo{
    font-family: "BTTF";
    color:#a3d2ca;
    -webkit-text-stroke: .1px black;
    text-decoration: none;
}

.licensePlate{
    height:60px;
    width:140px;
    background-color: white;
    position: absolute;
    bottom:300px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    border-radius: 5px;
    padding:3px;
}
