@font-face{
    font-family: "Sharpie";
    src: url("fonts/PermanentMarker-x99j.ttf");
}

body{
    background-image: url("https://www.transparenttextures.com/patterns/back-pattern.png"),linear-gradient(to bottom,#a07517,#5d30c7);
    padding-bottom:50px;
    padding-left:100px;
    padding-right:100px;
    font-family: "Sharpie";
}

.trunk{
    background-image: url("https://www.transparenttextures.com/patterns/back-pattern.png");
    background-color: white;
    border-radius: 16px;
    padding:15px;  
    min-width:340px;
}

.trunkList{
    display:flex;
    flex-direction: column;
    width:100%;
}

