.trunkProduct{
    flex:1;
    display:flex;
    align-items: center;
    background-image: url(./images1/background-crumpled-paper-sheet_1194-7545.jpg) ;
    border-radius: 16px;
    padding:15px;
    margin-bottom:5px;
    background-size: 100%;
    background-repeat: no-repeat;
    justify-content: space-between; 
}

.trunkProduct>a>img{
    width:150px;
    height:150px;
    object-fit: cover;
    border-radius: 3px;
}
a{
    color: black;
    text-decoration: none;
}

.itemInfo{
    flex:2;
    margin-left: 10px;
    margin-right: 10px;
}

.openDeloreanCart{
    position: relative;
    top:26px;
    width:90%;
    left:25px;
    flex:1 1;
}

.openDeloreanCartDiv{
    width: 200px;
    position: relative;
}



@media (max-width:720px){
    .openDeloreanCartDiv{
        width:150px;
        position:relative;
        bottom:7px;
    } 

} 

@media (max-width:671px){
    .openDeloreanCartDiv{
        width:130px;
        position:relative;
        bottom:11px;
    } 
       
} 

@media (max-width:619px){
    .openDeloreanCartDiv{
        width:120px;
        position:relative;
        bottom:11px;
    }   
} 

button{
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
}

button:focus {
    outline: 0;
}
.remove{
    position: relative;
    top:40px;
    font-family: "Sharpie";
}

.remove>img{
    width:30px;
    padding:10px;
    border-radius: 5px;
}

.remove>img:hover{
    width:30px;
    background-color:rgba(255, 94, 0, 0.932);
}

.total{
    text-align: right;
    margin-right:20px;
}


.checkoutButton{
    margin-top: 30px;
}

.checkoutButton:hover{
    color:rgb(64, 198, 221);
}