.productPage{
    background-image: url("https://www.transparenttextures.com/patterns/back-pattern.png");
    background-color: white;
    border-radius: 16px;
    display:flex;
    flex-wrap: wrap;
    padding:20px;
    
}
.previews{
    flex:1;
}

.productPageProduct{
    flex:1;
    background-image: url("./images1/productPageProductBackground.jpg");
    background-size: 100%;
    background-repeat: no-repeat;
    border-radius: 5px;
    min-width: 300px;
    

}

.productPageProduct>img{
    width:90%;
    object-fit: contain;
    margin-left: 5%;
    margin-right: 5%; 
    margin-top: 15%;
    border-radius: 5px;
   
}

.productPageProductButtons{
    display:flex;
    justify-content: space-between;
    margin-top:5%;
    margin-bottom:10%;
    align-content: center;

}

.productPageProductButtons>button{
    flex:1;
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;

}

.productPageProductButtons:hover {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
}

.productPageProductButtons>button>img{
    width:17%;
    object-fit: contain;
    padding:3px;
}

.productPageProductButtons>button>img:hover{
    background-color:rgb(64, 198, 221);
    border-radius: 5px;
}

.productPageProductButtons>button>p{
    font-family: "Sharpie";
    font-size: 130%;
}

.productPageProductButtons>button>p:hover{
    color:rgb(64, 198, 221);
}

.productPageDescription{
    flex:1;
    display:flex;
    flex-direction: column;
    font-family: "Sharpie";
    text-align:center;
    padding:10px;
    min-width: 300px;
}

.description{
    flex:3;
    margin-bottom:15px;
}

.size{
    flex:1;
    margin-bottom:15px;
}

.era{
    flex:1;
    margin-bottom:15px;
}

.notes{
    flex:2;
}




@media(max-width:549px){
    .productPage{
        width: 310px;  
    }
} 