@font-face{
    font-family: "Sharpie";
    src: url("fonts/PermanentMarker-x99j.ttf");
}


.listings{
    background-image: url("https://www.transparenttextures.com/patterns/back-pattern.png");
    background-color: white;
    border-radius: 16px;   
    min-width: 366px;
   
}


.deloreanCart{
    position: relative;
    top:48px;
    width:100%;
    left:25px;
    flex:1 1;
}

.deloreanCartDiv{
    width: 200px;
    position:relative;
}


.navbar{
    position: relative;
    display:flex;
    align-items: flex-end;
    animation: navbarAnimation linear 2s 1.1;
}


.navbarLogo{
    font-family: "BTTF";
    background-image: url(https://cdn.mos.cms.futurecdn.net/NUiwBaHbarqcAqqdpPqJu8-320-80.jpg);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    -webkit-text-stroke: .1px #a9294f;
    flex:1 1;
    text-decoration:none;

    
}

.navbarSearch{
    flex:3;
    padding-bottom:5px;
    margin-right: 0px;
    min-width: 134px;
    position:relative;
    left:15px;
}

.searchBar{
    width:100%;
    border:none;
    border-radius: 5px;
    height:20px;
}

ul{
    display:flex;
    flex:1 1;
}

ul li{
    flex:1;
    list-style: none;
    padding-bottom: 7px;
}

ul li a{
    text-decoration: none;
    color: black; 
    font-family: 'BTTF';
    font-size: small;
    -webkit-text-stroke: .1px #a9294f;
    padding: 1rem;
}

ul li a:hover{
    color:white;
}

input:focus{
    outline: none;
}

.aboutLink{
    padding-left: 0px;
}

.contactLink{
    padding-right:0px;
}


@media (max-width:849px){
    
    .navbar{
        flex-wrap: wrap;
    }

    ul{
        padding:0px;
    }

    .deloreanCart{
        top:50px;
        left:0px;
    }

    .navbarLogo{
        margin-right: 5px;
    }

    .navbarSearch{
        order:5;
        left:0px;
    }

    .navbarLogo{
        right:5px;
    }

    
} 

@media (max-width:720px){
    
    .navbarLogo{
        font-size: 1rem;
        top:23px;
    }
    .deloreanCartDiv{
        width:150px;
    } 

    .deloreanCart{
        bottom: 23px;
    }

    .basketAmount{
        top:-25px;
        
    }

    

} 

@media (max-width:671px){
    
    .navbarLogo{
        font-size: .9rem;
    }

    ul li a{
        font-size: .7rem;
    }

    .deloreanCartDiv{
        width:130px;
        position:relative;
        bottom:11px;
    } 
       
} 

@media (max-width:619px){
    

    .navbarLogo{
        font-size: .7rem;
    }
    .deloreanCartDiv{
        width:120px;
        position:relative;
        bottom:11px;
    } 

    ul li a{
        font-size: .6rem;
    }

    
    
} 
@media (max-width:564px){
    

    .navbar{
        width: 360px;
    }

    .navbarSearch{
        
        left:0px;
    }

    .navbarLogo{
        right:5px;
    }

    


    
} 

.aboutNav{
    margin-top:800px;
}

.about{
    margin-top:100px;
    text-align: center;
    margin-right:20%;
    margin-left:20%; 
}

.aboutTitle{
    margin-bottom:50px;
    font-family: "BTTF";
}

.aboutContent{
    font-family: 'Arvo', serif;
}

.contact{
    margin-top:100px;
    text-align: center;
    margin-right: 20%;
    margin-left:20%; 
}

.contactTitle{
    margin-bottom:50px;
    font-family: "BTTF";
}

.contactForm{
    display:flex;
    flex-direction: column;
}

.name{
    display:flex;
    flex-wrap: wrap;
}

.fname{
    margin-right: 5px;
}

.lname{
    margin-left: 5px;
}

.email{
    margin-top:10px;    
}

.subject{
    margin-top:10px;    
}

.message{
    margin-top:10px;
}

.submitButton{
    margin-top:10px;
    margin-bottom: 500px;
    background-image:url("https://www.transparenttextures.com/patterns/back-pattern.png");
    background-color:#a07517;
    color:white;
    height:30px;
    font-family:'Arvo', serif;
    border-radius:5px;
    border:none;
}

.submitButton:hover{
    background-color:#5c4616;
    
}

input{
    flex:1;
}

.contactInput{
    border-radius:3px;
    border:none;
}

@media (max-width:728px){
    .fname{
        margin-right: 2.5px;
        margin-left: 2.5px;
        margin-bottom:10px;
    }
    
    .lname{
        margin-right: 2.5px;
        margin-left: 2.5px;
    }

    .subject{
        margin-right: 2.5px;
        margin-left: 2.5px;
    }

    .email{
        margin-right: 2.5px;
        margin-left: 2.5px;
    }
}

.shoppingCart:hover{
    background-color:rgb(64, 198, 221);
    border-radius: 5px;
    padding:3px;
}

.basketAmount{
    position: relative;
    left:50px;
    bottom:45px;
   
}

.openBasketAmount{
    position: relative;
    left:10px;
    bottom:50px;
}
